// Imports
@import 'bootstrap/variables';
@import 'fonts';
@import 'vars';
@import 'mixins';

// Global animation
.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

@-webkit-keyframes fadeInUp {
  from {
    opacity: 0;
    /*-webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);*/
    -webkit-transform: translate3d(0, 50px, 0);
    transform: translate3d(0, 50px, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;

    -webkit-transform: translate3d(0, 50px, 0);
    transform: translate3d(0, 50px, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

.fadeInUp {
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
}

.animate-box {
	.js & {
		opacity: 0;
	}
}

// Global animation end

// Template Base
body {
	font-family: $font-primary;
	line-height: 1.6;
	font-size: 16px;
	background: $color__body--bg;
	color: #848484;
	font-weight: 300;
	overflow-x: hidden;
	&.lviv-offcanvas {
		overflow: hidden;
	}
}
a {
	color: $color__primary;
	@include transition(.5s, ease);
	&:hover{
		text-decoration: none ;
		color: $color__primary;
	}
}
p, ul, ol {
	margin-bottom: 1.5em;
	font-size: 16px;
	color: $color__text;
	font-family: $font-primary;
}

h1, h2, h3, h4, h5, h6 {
	color: $color__text;
	font-family: $font-primary;
	font-weight: 400;
	margin: 0 0 20px 0;
}
figure {
	margin-bottom: 2em;
}
::-webkit-selection {
  color: $color__body;
  background: $color__selection;
}

::-moz-selection {
  color: $color__body;
  background: $color__selection;
}

::selection {
  color: $color__body;
  background: $color__selection;
}
#lviv-work-section,
#lviv-blog-section,
#lviv-reasons,
#lviv-tours,
#lviv-testimonial,
.lviv-content-section {
	padding: 7em 0;
	@media screen and (max-width: $screen-sm) {
		padding: 4em 0;
	}

}

#lviv-wrapper {
	overflow-x: hidden;
	position: relative;
}
.lviv-offcanvas {
	overflow: hidden;
	#lviv-wrapper {
		overflow: hidden!important;
	}
}

#lviv-page {
	position: relative;
	@include transition(.5s);
	.lviv-offcanvas & {
		@include lviv-translateX(-240px);
	}
}

.lviv-section-gray{
	background: rgba($color__black,.04);
}

#lviv-header{
	position: relative;
	margin-bottom: 0;
	z-index: 9999999;
}

/* HEADER */

// Logo
#lviv-logo{
  position: relative;
  margin-top: 25px;
  font-weight: 700;
  float: left;
  padding-left: 40px;
  img {
	z-index: 2;
	font-size: 28px;
  height: 28px;
	text-transform: uppercase;}


	a{
		color: $color__primary;
		&:hover,&:focus{
			color: $color__primary !important;
		}
	}

}

// MENU
#lviv-menu-wrap {
	@media screen and (max-width: $screen-sm) {
		display: none;
	}
}
#offcanvas-menu {
	position: fixed;
	top: 0;
	bottom: 0;
	z-index: 99;
	background: #222;
	right: -240px;
	width: 240px;
	padding: 20px;
	overflow-y: scroll!important;
	display: none;
	@media screen and (max-width: $screen-sm) {
		display: block;
	}
	ul {
		padding: 0;
		margin: 0;
		li {
			padding: 0;
			margin: 0 0 10px 0;
			list-style: none;
			a {
				font-size: 18px;
				color: rgba(255,255,255,.4);
				&:hover {
					color: $color__primary;
				}
			}
			> .lviv-sub-menu {
				margin-top: 9px;
				padding-left: 20px;
			}
			&.active {
				> a {
					color: $color__primary;
				}
			}
		}
		.lviv-sub-menu {
			display: block!important;
		}
	}
}



/* BURGER MENU */
.lviv-nav-toggle {

  cursor: pointer;
  text-decoration: none;
  &.active i {
		&::before, &::after {
			background: $color__white;
		}
  }
  &.dark {
  	&.active i {
			&::before, &::after {
				background: $color__white;
			}
	  }
  }
  &:hover, &:focus, &:active {
  	outline: none;
  	border-bottom: none!important;
  }
  i {
  	position: relative;
	  @include inline-block;
	  width: 30px;
	  height: 2px;
	  color: $color__white;
	  font:bold 14px/.4 Helvetica;
	  text-transform: uppercase;
	  text-indent:-55px;
	  background: $color__white;
	  transition: all .2s ease-out;
		 &::before, &::after {
	  	  content:'';
		  width: 30px;
		  height: 2px;
		  background: $color__white;
		  position: absolute;
		  left:0;
		  @include transition(.2s);
	  }
  }
  &.dark {
  	i {
	  	position: relative;
		  color: $color__black;
		  background: $color__black;
		  transition: all .2s ease-out;
			 &::before, &::after {
			  background: $color__black;
			  @include transition(.2s);
		  }
	  }
  }
}

.lviv-nav-toggle i::before {
  top: -7px;
}
.lviv-nav-toggle i::after {
  bottom: -7px;
}
.lviv-nav-toggle:hover i::before {
  top: -10px;
}
.lviv-nav-toggle:hover i::after {
  bottom: -10px;
}
.lviv-nav-toggle.active i {
	background: transparent;
}
.lviv-nav-toggle.active i::before {
  top:0;
  -webkit-transform: rotateZ(45deg);
     -moz-transform: rotateZ(45deg);
      -ms-transform: rotateZ(45deg);
       -o-transform: rotateZ(45deg);
          transform: rotateZ(45deg);
}
.lviv-nav-toggle.active i::after {
  bottom:0;
  -webkit-transform: rotateZ(-45deg);
     -moz-transform: rotateZ(-45deg);
      -ms-transform: rotateZ(-45deg);
       -o-transform: rotateZ(-45deg);
          transform: rotateZ(-45deg);
}
.lviv-nav-toggle {
  	position: absolute;
  	top: 0px;
	right: 0px;
	z-index: 9999;
	display: block;
	margin: 0 auto;
	display: none;
	cursor: pointer;
  	@media screen and (max-width: $screen-sm) {
  		display: block;
  		top: 0px;
  		margin-top: 2em;
  	}

}

/* Superfish Override Menu */
.sf-menu {
	margin: 7px 0 0 0!important;
}

.sf-menu {
	float: right;
}
.sf-menu ul {
	box-shadow: none;
	border: transparent;
	min-width: 12em;
	*width: 12em;
}

.sf-menu a {
	color: rgba(0,0,0,.5);
	padding: .75em 1em!important;
	font-weight: normal;
	border-left: none;
	border-top: none;
	border-top: none;
	text-decoration: none;
	zoom: 1;
	font-size: 16px;
	border-bottom: none!important;
	font-weight: bold;

	&:hover,&:focus{
		color: $color__primary!important;
	}
}
.sf-menu li.active{
	a {
		&:hover,
    &:focus{
			color: $color__primary;
		}
	}
}

.sf-menu li,
.sf-menu ul li,
.sf-menu ul ul li,
.sf-menu li:hover,
.sf-menu li.sfHover  {
	background: transparent;
}
.sf-menu ul li a,
.sf-menu ul ul li a {
	text-transform: none;
	padding: .20em 1em!important;
	letter-spacing: 1px;
	font-size: 14px!important;
}
.sf-menu li:hover a,
.sf-menu li.sfHover a,
.sf-menu ul li:hover a,
.sf-menu ul li.sfHover a,
.sf-menu li.active a {
	color: $color__primary;
	text-shadow: 2px 2px 1px rgba(0, 0, 0, .08);
}
.sf-menu ul li:hover,
.sf-menu ul li.sfHover {
	background: transparent;
}


.sf-menu ul li {
	background: transparent;
}
.sf-arrows .sf-with-ul {
	padding-right: 2.5em;
	*padding-right: 1em;
}
.sf-arrows .sf-with-ul:after {
	content: '';
	position: absolute;
	top: 50%;
	right: 1em;
	margin-top: -3px;
	height: 0;
	width: 0;
	border: 5px solid transparent;
	border-top-color: #ccc;
}

.sf-arrows > li > .sf-with-ul:focus:after,
.sf-arrows > li:hover > .sf-with-ul:after,
.sf-arrows > .sfHover > .sf-with-ul:after {
	border-top-color: #ccc;
}
.sf-arrows ul .sf-with-ul:after {
	margin-top: -5px;
	margin-right: -3px;
	border-color: transparent;
	border-left-color: #ccc;
}
.sf-arrows ul li > .sf-with-ul:focus:after,
.sf-arrows ul li:hover > .sf-with-ul:after,
.sf-arrows ul .sfHover > .sf-with-ul:after {
	border-left-color: #ccc;
}

#lviv-header-section{
	padding:0;
	position: relative;
	left: 0;
	width: 100%;
	z-index: 1022;
	background: $color__white;
}
.nav-header{

	position: relative;
	float: left;
	width: 100%;
}


#lviv-menu-wrap {
	position: relative;
	z-index: 2;
	display: block;
	float: right;
	margin-top: 15px;
	.sf-menu a {
		padding: 1em .75em;
	}
}
@media screen and (max-width: 768px) {
#lviv-menu-wrap {
    display: none;
    top: 0;
    margin-top: 2em;
}}
#lviv-primary-menu {
	> li {
		> ul {
			li {
				&.active > a {
					color: $color__primary!important;
				}
			}
		}
		> .sf-with-ul {
			&:after {
				border: none!important;
				@include icomoon();
				position: absolute;
				float: right;
				margin-right: -0px;
				top: 50%;
				font-size: 12px;
				margin-top: -7px;
				content: "\e922";
				color: rgba(0,0,0,.5);
			}
		}
		> ul {
			li {
				position: relative;
				> .sf-with-ul {
					&:after {
						border: none!important;
						@include icomoon();
						position: absolute;
						float: right;
						margin-right: 10px;
						top: 20px;
						font-size: 12px;
						content: "\e924";
						color: rgba(0,0,0,.5);
					}
				}
			}
		}
	}
}
#lviv-primary-menu .lviv-sub-menu {
	padding:10px 7px 10px;
	background: $color__white;
	left: 0;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
	border-radius: 5px;
	-webkit-box-shadow:0 5px 15px rgba(0,0,0,0.15);
	-moz-box-shadow:0 5px 15px rgba(0,0,0,0.15);
	-ms-box-shadow:0 5px 15px rgba(0,0,0,0.15);
	-o-box-shadow:0 5px 15px rgba(0,0,0,0.15);
	box-shadow:0 5px 15px rgba(0,0,0,0.15);
	> li {
		> .lviv-sub-menu {
			left: 100%;
		}
	}

}

#lviv-primary-menu .lviv-sub-menu:before {
	position:absolute;
	top:-9px;
	left:20px;
	width:0;
	height:0;
	content:'';
}

#lviv-primary-menu .lviv-sub-menu:after {
	position:absolute;
	top:-8px;
	left:21px;
	width:0;
	height:0;
	border-right:8px solid transparent;
	border-bottom:8px solid $color__white;
	border-bottom:8px solid rgba(255, 255, 255, 1);
	border-left:8px solid transparent;
	content:'';
}

#lviv-primary-menu .lviv-sub-menu .lviv-sub-menu:before {
	top:6px;
	left:100%;
}

#lviv-primary-menu .lviv-sub-menu .lviv-sub-menu:after {
	top:7px;
	right:100%;
	border: none!important;
}

.site-header.has-image #primary-menu .sub-menu {
	border-color:#ebebeb;
	-webkit-box-shadow:0 5px 15px 4px rgba(0,0,0,0.09);
	-moz-box-shadow:0 5px 15px 4px rgba(0,0,0,0.09);
	-ms-box-shadow:0 5px 15px 4px rgba(0,0,0,0.09);
	-o-box-shadow:0 5px 15px 4px rgba(0,0,0,0.09);
	box-shadow:0 5px 15px 4px rgba(0,0,0,0.09);
	&:before {
		display:none;
	}
}
#lviv-primary-menu .lviv-sub-menu a {
	letter-spacing: 0;
	padding:0 15px;
	font-size:18px;
	line-height:34px;
	color: #575757 !important;
	text-transform:none;
	background:none;
	&:hover {
		color: $color__primary!important;
	}
}
/* HEADER END*/

