//TOP SPOTS
#lviv-topspots{
	width: 100%;
	padding: 0;
	margin: 0;
}

#lviv-topspots-list {
	@include flexwrap;
	@include flex;
	position: relative;
	float: left;
	padding: 0 !important;
	margin: 0 !important;
	width: 100%;
	li {
		display: table;
		padding: 0;
		margin: 0;
		list-style: none;
		min-height: 220px;
		background-position: center center;
		background-size: cover;
		background-repeat: no-repeat;
		float: left;
		clear: left;
		position: relative;
		@media screen and (max-width: $screen-xs) {
			margin-left: 0;
		}
		.title-bg{
			min-height: 220px;
			padding: 2em;
			position: relative;
			width: 100%;
			display: table-cell;
			vertical-align: middle;
			background: $color__secondary;
			z-index: 12;
			h2{
				color: $color__white;
		   	margin-bottom: 20px;
		   	font-size: 40px;
			}
			span{
	   		text-transform: uppercase;
	   		letter-spacing: 2px;
	   		a{
	   			color: $color__white;
	   			font-size: 13px;
	   			font-weight: 400;
	   			border: 1px solid $color__white;
	   			padding: 5px 7px;
	   			@include border-radius(4px);
	   		}
	   	}
	   	@media screen and (max-width: $screen-sm){
	   		h2{
	   			font-size: 24px;
	   		}
	   	}
		}
		&:after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			top: 0;
			background: rgba(0, 0, 0, .4);
			z-index: 8;
			@include transition(.5s)
		}
		> a {
			min-height: 220px;
			padding: 2em;
			position: relative;
			width: 100%;
			display: table-cell;
			vertical-align: middle;
		   z-index: 12;
		   &:after{
				position: absolute;
				top: 20px;
				bottom: 20px;
				left: 20px;
				right: 20px;
				content: "";
				border: 2px solid rgba($color__white,.8);
				opacity: 0;
				@include transition(.3s);
			}

			.case-studies-summary {
			   width: auto;

			   @media screen and (max-width: $screen-sm) {
			   	bottom: 1em;
				   left: 1em;
				   right: 1em;
			   }
			   span {
			   	text-transform: uppercase;
			   	letter-spacing: 2px;
			   	font-size: 13px;
			   	color: rgba(255,255,255,.7);
			   }
			   h2 {
			   	color: $color__white;
			   	margin-bottom: 0;
			   	font-size: 24px;
			   	font-weight: 400;
			   	@include transition(.1s);
			   	@media screen and (max-width: $screen-sm) {
			   		font-size: 24px;
			   	}
			   }
			}
		}
		&:hover{
			&:after{
				-webkit-box-shadow: inset 0px 0px 91px 0px rgba(0,0,0,0.75);
				-moz-box-shadow: inset 0px 0px 91px 0px rgba(0,0,0,0.75);
				-ms-box-shadow: inset 0px 0px 91px 0px rgba(0,0,0,0.75);
				-o-box-shadow: inset 0px 0px 91px 0px rgba(0,0,0,0.75);
				box-shadow: inset 0px 0px 91px 0px rgba(0,0,0,0.75);
				background: rgba(0, 0, 0, .3);
			}
			> a{
				&:after{
					opacity: 1;
				}
				.case-studies-summary {
					h2{
						margin-top: -5px;
					}
				}
			}
		}
		&.two-third {
			width: 65.6%;
			@media screen and (max-width: $screen-sm) {
				width: 49%;
			}
			@media screen and (max-width: $screen-xs) {
				width: 100%;
			}
		}
		&.one-third {
			width: 32.3%;
			@media screen and (max-width: $screen-sm) {
				width: 49%;
			}
			@media screen and (max-width: $screen-xs) {
				width: 100%;
			}
		}
		&.one-forth {
			width: 25%;
			@media screen and (max-width: $screen-sm) {
				width: 49%;
			}
			@media screen and (max-width: $screen-xs) {
				width: 100%;
			}
		}
		&.one-half {
			width: 50%;
			@media screen and (max-width: $screen-xs) {
				width: 100%;
			}
		}

	}

}
//TOP SPOTS END


